import {RecycleList, ScreenWrapper} from '@true/ui';
import React, {memo, useCallback, useEffect, useState} from 'react';
import {
  Colors,
  Fonts,
  goBack,
  styled,
  useNavigationParams,
  useTheme,
} from '@true/core';

import {useAnimatedSafeAreaInsets} from 'react-native-safe-area-context';
import Animated, {useAnimatedStyle} from 'react-native-reanimated';
import {MySearchBar} from '@/screens/SearchScreen/SearchBar';
import {requestSearchUpdates} from '@/store/updates/function';
import {useAsyncFn} from '@/hooks/useAsyncFn';
import SkeletonPlaceholder from 'react-native-skeleton-placeholder';
import Metrics from '@/assets/Metrics';
import {View} from 'react-native';
import {useUpdatesByQuery} from '@/store/updates';
import {PostThumbnail} from '@/components/PostThumbnail';
import {EmptySearchView} from '@/components/EmptyView';
import {IFilterParams} from '@/screens/SearchScreen/type';
import {FilterIcon} from '@/components/FilterIcon';
import {navigateToFilterScreen} from '@/utils';
import {IMAGE_EMPTY_SEARCH} from '@true/asset';

export interface SearchScreenParams {
  isFilter: boolean;
}
export const SearchScreen = memo(function () {
  const {isFilter} = useNavigationParams<SearchScreenParams>();
  const {aTop} = useAnimatedSafeAreaInsets();
  const animatedPaddingTop = useAnimatedStyle(() => ({
    paddingTop: aTop.value,
  }));
  const searchUpdatesIds = useUpdatesByQuery('search');
  const [params, setParams] = useState<IFilterParams>({});
  const [isTexting, setTexting] = useState(false);
  const [value, setValue] = useState('');

  const [{loading}, onRequest] = useAsyncFn(async (_value: any) => {
    const _params: any = {
      q: _value.q,
    };
    await requestSearchUpdates(_params);
  }, []);

  const onChangeText = useCallback(
    async (q: string) => {
      await onRequest({...params, q});
      setTexting(false);
      setValue(q);
    },
    [onRequest, params],
  );
  const onFilter = useCallback(
    async (_value: any) => {
      setTexting(true);
      // await onRequest({..._value, q: value});
      setTexting(false);
      setParams(_value);
    },
    [onRequest, value],
  );

  const renderItem = useCallback(
    (id: number, index?: number) => (
      <PostThumbnail isInFeed={true} id={id} key={index} />
    ),
    [],
  );

  useEffect(() => {
    onRequest({
      q: '',
    }).then();
  }, [onRequest]);

  return (
    <ScreenWrapper>
      <SHeader style={animatedPaddingTop}>
        <SSearchView>
          <MySearchBar
            isTexting={isTexting}
            setTexting={setTexting}
            onSearchTextChange={onChangeText}
          />
        </SSearchView>
        {isFilter ? (
          <FilterIcon
            params={params}
            onPress={() => {
              navigateToFilterScreen({
                params,
                setParams: onFilter,
              });
            }}
          />
        ) : null}
        <CancelButton onPress={goBack}>
          <SCancelText>Cancel</SCancelText>
        </CancelButton>
      </SHeader>
      <View
        style={{
          flex: 1,
          backgroundColor: Colors.neutral6,
        }}>
        {!isTexting && !loading && (
          <RecycleList
            data={searchUpdatesIds}
            itemHeight={100}
            renderItem={renderItem}
            showsVerticalScrollIndicator={false}
            contentContainerStyle={{
              justifyContent: 'center',
              flex: 1,
            }}
            loading={false}
            isRefreshing={false}
            isLoadMore={false}
            error={false}
            refreshable={false}
            loadMorable={false}
            ListHeaderComponent={<View style={{width: '100%', height: 16}} />}
            // ListFooterComponent={<STextGrey>End of result</STextGrey>}
            renderEmptyView={() => (
              <EmptySearchView
                image={IMAGE_EMPTY_SEARCH}
                title={'Empty updates in this type'}
              />
            )}
          />
        )}
        {(isTexting || loading) && (
          <>
            {Array.apply(null, Array(6)).map((item, index) => (
              <SkeletonPlaceholder key={index} borderRadius={4}>
                <SkeletonPlaceholder.Item
                  padding={6}
                  flexDirection="row"
                  alignItems="center">
                  <SkeletonPlaceholder.Item
                    width={40}
                    height={40}
                    borderRadius={7}
                  />
                  <SkeletonPlaceholder.Item marginLeft={8}>
                    <SkeletonPlaceholder.Item
                      width={Metrics.screen.width * 0.7}
                      height={20}
                    />
                    <SkeletonPlaceholder.Item
                      width={Metrics.screen.width * 0.4}
                      height={20}
                      marginTop={4}
                    />
                  </SkeletonPlaceholder.Item>
                </SkeletonPlaceholder.Item>
              </SkeletonPlaceholder>
            ))}
          </>
        )}
      </View>
    </ScreenWrapper>
  );
});

const SHeader = styled(Animated.View)`
  flex-direction: row;
  align-items: center;
  margin-bottom: -8px;
  padding-bottom: 8px;
  border-bottom: 1px solid ${p => p.theme.neutral4};
  background-color: white;
`;
const SSearchView = styled.View`
  flex: 1;
  padding: 8px 16px;
  border-radius: 8px;
`;

const CancelButton = styled.TouchableOpacity`
  display: flex;
  padding: 4px 0px;
  justify-content: center;
  align-items: center;
  margin-right: 12px;
`;
const SCancelText = styled.Text`
  color: ${p => p.theme.neutral2};
  text-align: center;
  font-family: ${Fonts.Regular};
  font-size: 14px;
  font-weight: 500;
  line-height: 24px; /* 171.429% */
`;
